.nav-title {
    font-size: 36px;
    font-weight: 700;
    display: inline-block;
    color: #f2f2f2;
    padding-left: 10px;
    position: absolute;
    top: 5vh;
    margin-top: 0;
}

.header {
  /* overflow: hidden; */
  background-color: #2189c1;
  height: 15vh;
}

.nav-link {
  font-family: 'Roboto Slab';
  float: left;
  color: #f2f2f2;
  background-color: #2189c1;
  text-align: center;
  padding: 24px 10px;
  text-decoration: none;
  font-size: 15px;

  /* these last two make the logout button in
  the nav bar look like another link */
  border:none;
  cursor: pointer;
  outline:0;
}

.nav-link:hover {
  color: lightgray;
}

.nav-right {
  float: right;
}

.nav-logo {
  height: 12vh;
  width: 12vh;
  padding: 1vh;
}