.modal {
  position: fixed;
  top: 0;
  left: 0;
  width:100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}

.modal-main {
  position:fixed;
  background: white;
  width: 40vw;
  height: auto;
  top:50%;
  left:50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  padding: 30px;
  text-align: center;
}

.modal-main button {
  width: 20vw;
  height: 40px;
  margin-top: 20px;
  background-color: rgba(33, 137, 193, 1);
  color: white;
  font-size: 1.3rem;
  border: none;
  border-radius: 2px;
  box-shadow: 2px 2px 2px lightgray;
  cursor: pointer;
}

.modal-main h4 {
  font-size: 2rem;
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}
form#checkoutForm {
  width: 100%;
  height: 48vh;
  background-color: white;
  margin: 0;
  padding: 0;
  position: relative;
}

#checkoutForm button {
  width: 80%;
  height: 40px;
  padding: 3vh auto 0 auto;
  background-color: rgba(33, 137, 193, 1);
  color: white;
  font-size: 1.3rem;
  border: none;
  border-radius: 2px;
  cursor: pointer;
  position: absolute;
  bottom: 0;
  right: 8%;
}

.stripeInputWithLogo {
  display: flex;
  flex-direction: row;
  width: 100%;
  position: relative;
}

.StripeElement {
  width: 41vw;
  padding: 2%;
  border: 1px solid gainsboro;
  border-radius: 5px;
}

.StripeElement--complete {
  color: rgba(14, 124, 0, 0.5)
}

.StripeElement--invalid {
  color: rgb(255, 0, 0, .5)
}

#checkoutForm img {
  position: absolute;
  bottom: 1vh;
  right: 0;
}
.hidden {
  visibility: hidden;
}

a {
  text-decoration: none;
}
