body {
    font-family: 'Roboto Slab','Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin: 0px;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Krub';
}

#react-root {
    height: 100vh;
}

.site-content {
    height: 71vh;
    margin: 0;
    padding: 2vh;
    background-image: url(/static/media/coffee-background.6806419b.jpg);
    background-color: gray;
    background-repeat: no-repeat;
    background-size: 100% 100%;
}

.site-content h1 {
    margin-top: 0;
}

.alert {
    background-color: #ff4545;
    margin: 50px;
}

form {
    background-color: #dbd5d5;
    width: 30%;
    height: 250px;
    margin: 20px auto;
    padding: 25px;
    border-radius: 2px;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
}

input {
    margin: 5px;
    margin-bottom: 10px;
}

.log-in, .log-out, .register {
  background-color: #2189c1;
  border-radius: 4px;
  color: #f8f8f8;
  min-height: 50px;
  min-width: 100px;
  outline:0;
  cursor: pointer;
  font-size: 1.2rem;
  margin-top: 5vh;
}

.log-in:hover, .log-out:hover, .register:hover {
  background-color: #2189c1;
}

/* This line makes the "Register" and "Login" buttons look like links */
.link-button {
    background:none;
    color:inherit;
    border:none;
    padding:0;
    font: inherit;
    outline:0;
    border-bottom:1px solid #444; 
    cursor: pointer;
}

.dark-toast {
    background-color: rgba(50,50,50,.95);
    color: white;
    text-align: center;
    font-family: 'Krub';
}

.warning-toast {
    background-color: rgba(218, 165, 32, .9);
    color: black;
    text-align: center;
    font-family: 'Krub';
}
